import config from '@/app.config.js'

let lifeData = uni.getStorageSync('lifeData') || {};

let $app = lifeData.$app || {};

export default {
	//  namespaced: true 
	namespaced: true,
	/**
	 * vuex
	 */
	state: {
		/**
		 * 
		 * js
		 * vk.getVuex('$app.inited');
		 * 
		 * {{ vk.getVuex('$app.inited') }}
		 * js
		 * vk.setVuex('$app.inited', true);
		 */
		inited: $app.inited || false,
		config:{
			...config
		},
		//  vk.getVuex('$app.color.main') vk.setVuex('$app.color.main','#ff4444')
		color: $app.color || config.color,
		/**
		 * vk.getVuex('$app.originalPage');
		 * vk.setVuex('$app.originalPage', originalPage);
		 */
		originalPage: $app.originalPage || null, // 
	},
	/**
	 * (state)state
	 */
	getters: {

	},
	/**
	 * (action)
	 *  mutation   (type)    (handler)
	 *  state 
	 */
	mutations: {

	},
	/**
	 * mutation ==
	 * 1. Action  mutation
	 * 2. Action 
	 */
	actions: {

	}
}
