/**
 * 11
 * @description  : 1
 * @param {String || Number} 	startTime	 
 * vk.pubfn.dateDiff(startTime);
 */
pubfn.dateDiff = function(startTime, suffix = "") {
	if (!startTime) {
		return "";
	}
	if (typeof startTime === "string" && !isNaN(startTime)) startTime = Number(startTime);
	if (typeof startTime == "number") {
		if (startTime.toString().length == 10) startTime *= 1000;
		startTime = new Date(startTime);
		startTime = pubfn.timeFormat(startTime);
	}
	if (typeof startTime == "string") {
		startTime = startTime.replace("T", " ");
		startTime = startTime;
		startTime = new Date(startTime.replace(/-/g, "/")); //-/new Date
	}
	let endTime = new Date(); //
	let nd = 1000 * 24 * 60 * 60; //
	let nh = 1000 * 60 * 60; //
	let nm = 1000 * 60; //
	let ns = 1000; //long diff;try {
	let diff = endTime.getTime() - startTime.getTime();
	let day = Math.floor(diff / nd); //
	let hour = Math.floor(diff % nd / nh); //
	let min = Math.floor(diff % nd % nh / nm); //
	let sec = Math.round(diff % nd % nh % nm / ns); ////
	let showStr = "1 ";
	if (day > 0) {
		showStr = day + "";
	} else if (hour > 0) {
		showStr = hour + "";
	} else if (min > 0) {
		showStr = min + "";
	} else if (sec > 0) {
		showStr = sec + "";
	}
	showStr += suffix;
	return showStr;
}
