/**
 * 
 * @description 
 * :
 * 3210 -> 3
 * 31210 -> 3
 * 1523412 -> 1
 * 15234120 ->1
 * @param {Number} n 
 * vk.pubfn.numStr(n);
 */
pubfn.numStr = function(n) {
	if (typeof n == "string") {
		n = parseFloat(n);
	}
	let str = n;
	if (n < 1000) {
		str = n;
	} else if (n < 10000) {
		let n1 = Math.floor(n / 100);
		str = n1 / 10 + ""
	} else if (n < 1000000) {
		let n1 = Math.floor(n / 1000);
		str = n1 / 10 + ""
	} else if (n < 10000000) {
		let n1 = Math.floor(n / 1000000);
		str = n1 + ""
	} else if (n < 100000000) {
		let n1 = Math.floor(n / 10000000);
		str = n1 + ""
	} else if (n >= 100000000) {
		let n1 = Math.floor(n / 10000000);
		str = n1 / 10 + ""
	}
	return str;
};
/**
 * 100  1
 * @param {Number} money 
 * vk.pubfn.priceFilter(money);
 */
pubfn.priceFilter = function(money, defaultValue = "") {
	if (pubfn.isNull(money)) {
		return defaultValue;
	}
	if (isNaN(money)) {
		return money;
	}
	if (typeof money == "string") {
		money = parseFloat(money);
	}
	return (money / 100).toFixed(2);
};
