/**
 * 
 * @param {String} name client/pub 
 * @example const pubObject = uni.vk.importObject('client/pub'); // 
 *  async 
async test(){
	let res = await pubObject.getList({
		title: "",
		data: { 
			a: 1,
			b: "2"
		}
	});
}
 */
var importObject = function(name, importObjectOptions = {}) {
	const newObj = new Proxy(importObject, {
		get: function(target, key, receiver) {
			/**
			 * 
			 * @param {Object}   data       { a:1, b:"2" }  let { a, b } = data; 
			 * @param {String}   title     
			 * @param {Boolean}  needAlert truetrue
			 * @param {Object}   loading   title { name: "loading", that: that }  namethatthat[name] = true; that[name] = false;
			 */
			return async function(options) {
				// importObjectOptionseasytrueoptions
				if (importObjectOptions.easy) {
					delete importObjectOptions.easy;
					options = {
						data: options
					}
				}
				// importObjectOptionsdata
				if (importObjectOptions.data) {
					options.data = Object.assign(importObjectOptions.data, options.data)
				}
				return uni.vk.callFunction({
					...importObjectOptions,
					...options,
					url: `${name}.${key}`
				});
			}
		},
		// set: function(target, key, value, receiver) {
		// 	console.log("set");
		// 	console.log("target",target);
		// 	console.log("key",key);
		// 	console.log("value",value);
		// 	console.log("receiver", receiver);
		// },
	});
	return newObj;
};

export default importObject;